import * as React from 'react';
// import { PropTypes } from 'react'
import * as jsPDF from 'jspdf'
// import 'jspdf-autotable';
import html2canvas from 'html2canvas';
import htmlDocx from 'html-docx-js/dist/html-docx';
import { saveAs } from 'file-saver';
import moment from 'moment';

export interface HtmlToPDFProps {
    html?: any,
    renderType: string,
    resetFlag: any,
    fileType?: string | null | undefined,
    clientID: string
}

export class HtmlToPDFRenderer extends React.Component<HtmlToPDFProps>  {
    constructor(props: HtmlToPDFProps) {
        super(props)

    }
    
    componentWillReceiveProps() {
        if (typeof this.props.fileType == "string" && this.props.fileType.toLowerCase() == "docx") {
            this.printDocx(this.props.html);
        } else {
            this.printPdf(this.props.html);
        }
    }

    componentDidMount() {
        if (typeof this.props.fileType == "string" && this.props.fileType.toLowerCase() == "docx") {
            this.printDocx(this.props.html);
        } else {
            this.printPdf(this.props.html);
        }    }

    printPdf = (htmlData) =>  {
        // renderProposal
        let fileName = 'Proposal-' + this.props.clientID + '-' + moment().format('MMDDYYYY-hmssa');
        if (this.props.renderType == 'renderProposal') {
        let el =  document.createElement('div');
        el.setAttribute("id", "htmlPdfNode");
        el.style.cssText = "position:absolute; top: 0; left:-100%;";
        el.innerHTML = htmlData
        el.querySelectorAll('.rendererContainer').forEach((d,i) => {
            d.setAttribute('id',`rendererContainer${i}`);
            el.append(d);
        })
        document.getElementsByTagName('BODY')[0].append(el); 
        const query = document.querySelectorAll('.rendererContainer')
        let pdf = new jsPDF('p', 'mm');
        let pdfWidth = pdf.internal.pageSize.getWidth();
        let pdfHeight = pdf.internal.pageSize.getHeight();
        let heightLeft = pdfHeight;
        const posTop = 10;
        let pos = {
            left: 15,
            top: posTop
        }
        query.forEach((d, i) => {
            console.log(d)
            const item = document.getElementById(`rendererContainer${i}`) as HTMLElement;
            item !== null && html2canvas(item, {imageTimeout: i == 0 ? 0 : 15000})
            .then((canvas) => {
                let imgHeight = canvas.height * pdfWidth / canvas.width - (pos.left / 3.5);
                pos.top = heightLeft === pdfHeight ? posTop : pdfHeight - heightLeft + posTop
                // console.log(pdfHeight - heightLeft, heightLeft - imgHeight >= 0)
                const contentDataURL = canvas.toDataURL('image/png');
                // console.log(contentDataURL, i)
                if (heightLeft - imgHeight >= 0) {
                    pdf.addImage(contentDataURL, 'PNG', pos.left, pos.top, pdfWidth - (pos.left * pos.left / (pos.left / 2)), imgHeight);
                } else {
                    heightLeft = pdfHeight;
                    pos.top = posTop;
                    pdf.addPage();
                    pdf.addImage(contentDataURL, 'PNG', pos.left, pos.top, pdfWidth - (pos.left * pos.left / (pos.left / 2)), imgHeight);
                }
                heightLeft -= imgHeight;
            })
        })
        setTimeout(() => {
            pdf.save(fileName);
            document.querySelectorAll(".rendererContainer").forEach(d => {
                d.remove();
                console.log(d)
            })
            let docu = document.getElementById('htmlPdfNode') as HTMLElement;
            docu.remove();
        },2500)
    } else if (this.props.renderType == 'renderContract') {
        let pdf = new jsPDF("p", "pt", "letter");
        let wFooter = `
        ${htmlData}
        <footer>
            <i><small>Please note that the benefit plan / package stated herein are subject to change until the final conforme has been signed and agreed upon.</small></i>
        </footer>`
        let margins = {
            top: 40,
            bottom: 40,
            left: 40,
            width: 522
        };
        pdf.setFontSize(9);
        pdf.fromHTML(
            wFooter,
            margins.left,
            margins.top,
            {
              width: margins.width
            },
            function(dispose) {
              console.log(dispose)
              pdf.save(fileName+".pdf");
            },
            margins
        );
    }

    if (this.props.resetFlag) {
        this.props.resetFlag();
        }
    }

    printDocx = (htmlData) => {
        let fileName = 'Proposal-' + this.props.clientID + '-' + moment().format('MM/DD/YYYY h:m:ss a');
        if (this.props.renderType === 'renderProposal') {
            var converted = htmlDocx.asBlob(htmlData, {orientation: 'portrait', margins: {top: 720}});
            saveAs(converted, fileName+'.docx');
        } else if (this.props.renderType === 'renderContract') {
            let contractDocx = `
            <html xmlns:v="urn:schemas-microsoft-com:vml"
            xmlns:o="urn:schemas-microsoft-com:office:office"
            xmlns:w="urn:schemas-microsoft-com:office:word"
            xmlns:m="http://schemas.microsoft.com/office/2010/12/omml"
            xmlns="http://www.w3.org/TR/REC-html40">
            <head><meta http-equiv=Content-Type content="text/html; charset=utf-8"><title></title>
            <style>
            @page Section1 {
                margin:0.75in 0.75in 0.75in 0.75in;
                size:841.7pt 595.45pt;
                mso-page-orientation:portrait;
                mso-header-margin:0.5in;
                mso-header: h1;
                mso-footer-margin:0.5in;
                mso-footer: f1;
            }
            div.Section1 {page:Section1;}
            </style>
            </head>
            <body>
            <div class=Section1>
            <table style='margin-left:50in;'><tr style='height:1pt;mso-height-rule:exactly'>
                <td>
                  <div style='mso-element:header' id=h1>
                    <p class=headerFooter>
                        
                     </p>
                  </div>
                  &nbsp;
                </td>
            
                <td>
                  <div style='mso-element:footer' id=f1>
                    <p class=headerFooter>
                        <i><small>Please note that the benefit plan / package stated herein are subject to change until the final conforme has been signed and agreed upon.</small></i>
                     </p>
                  </div>
                  &nbsp;
            </td></tr></table>
            ${htmlData}
            </div></body>
            </html>
            `;
            var converted = htmlDocx.asBlob(contractDocx, {orientation: 'portrait', margins: {top: 720}});
            saveAs(converted, fileName+'.docx'); 
        }
        
    if (this.props.resetFlag) {
        this.props.resetFlag();
        }
    }

    render() {
        return (
            <>
            </>
        )
    }  
}   
 



