import * as React from 'react';
// ES modules
import ReactDOMServer from 'react-dom/server';
// CommonJS
// var ReactDOMServer = require('react-dom/server');
import { HtmlToPDFRenderer } from '../HtmlToPdfRenderer';
import RendererComponent from '../RendererComponent';
import { API } from 'Pages/API';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Button } from '@material-ui/core';
import {
    faDownload
} from '@fortawesome/free-solid-svg-icons';
import '../RendererComponent/styles.scss'

export interface RenderProps  {
    renderType: string,
    objId: string,
    label?: string,
    hideButton?: boolean,
    download?: boolean,
    setDownload?: any,
    fileType?: string, // DOCX or PDF
    clientID: string
}

export interface RenderState {
    reset?: any,
    treeObj: Array<any>
}

export class RendererClass extends React.Component<RenderProps, RenderState, any>  {

    constructor (props: RenderProps) {
        super(props)
        this.state = {
            reset: false,
            treeObj: []
        }
    }

    componentDidMount() {
        this.setState({
            reset: false
        })
    }

    fetchTreeObj = () => {
        let objId = this.props.objId;
        let renderType = this.props.renderType;
        // contract
        // let objId = '5db97be200884e7bb29aa6af'; 
        // proposal 
        // let objId = '5da41b18f510d90b0051aae7';
        // let renderType = 'renderContract';

        API.getTreeObj(objId, renderType).then((resp: any) => {
            if(resp && resp.error === undefined){
                console.log('success')
                this.setState({
                    treeObj: resp,
                    reset : true
                })
            } else {
                console.log(`Response error ${resp.error}`)
            }
        }).catch(err => console.log(err))
    }

    setReset = () => {
        console.log('setReset');
        this.setState({
            reset: false,
            treeObj: []
        });
    }

    traverse = (tree_id: string, consist?: Array<object>) => {
        let render: Array<any> = [];
        let type = this.props.renderType;
        // let treeObj = this.state.treeObj;
        let treeObj = this.state.treeObj;
        // let type = 'renderContract';
        if (consist) render = consist // set render value to consist if present to avoid clearing the variable with existing renderProposal

        for(var i = 0; i < treeObj.length; i++) {
            if (treeObj[i].tree_id === tree_id) {
                let parentNode = treeObj[i];

                if (parentNode[type]) {
                    render.push(JSON.parse(parentNode[type]));
                }

                if (parentNode.children.length > 0) {
                    for(var j = 0; j < parentNode.children.length; j++) {
                        let childId = parentNode.children[j];
                        let childNode = treeObj.find(c => {
                            return c._id === childId
                        });
    
                        if (childNode !== undefined) {
                            // call traverse again with the child's tree_id to recursively descend to object
                            this.traverse(childNode.tree_id, render);
                        }
                    }
                }
            }
        }
        console.log(render , 'render');
        return render;
    }

    ElToString(renderType) {
        // renderType = 'renderProposal'
        if (renderType == 'renderProposal') {
            return ReactDOMServer.renderToStaticMarkup(
                <>
                    <RendererComponent renderProposal={'headerProposal'}></RendererComponent>
                    <RendererComponent renderProposal={this.traverse('MCT-CT-BASE-RB')}></RendererComponent>
                    <RendererComponent renderProposal={this.traverse('MCT-CT-BASE-CV-SD')}></RendererComponent>
                </>
            )
        } else if (renderType == 'renderContract') {
            return ReactDOMServer.renderToStaticMarkup(
                <> 
                    <RendererComponent renderContract={this.traverse('MCT-CT-BASE-EX-TP')}></RendererComponent>
                    <RendererComponent renderContract={this.traverse('MCT-CT-BASE-DN-DD')}></RendererComponent>
                </>
            ) 
        } else {
            return null;
        }
    }

    handleOnClick = () => {
        // console.log("true")
        this.fetchTreeObj()
    }

    render (): JSX.Element {
        if (this.props.download && this.props.download === true && this.state.reset === false) {
            console.log('setDownload');
            this.props.setDownload(false);
            this.fetchTreeObj();
        }

        return (
            <>
                {
                    !this.props.hideButton ? <Button className={'downloadProposal'} onClick={()=> this.handleOnClick()}>
                            <FontAwesomeIcon
                             icon={faDownload}
                            ></FontAwesomeIcon>
                            {this.props.label ? this.props.label : 'Download pdf'}
                        </Button> : null
                }     
                {
                    this.state.reset === true && 
                    <HtmlToPDFRenderer clientID={this.props.clientID} fileType={this.props.fileType ? this.props.fileType : 'PDF'} html={ this.ElToString(this.props.renderType ? this.props.renderType : 'renderProposal') } renderType={this.props.renderType ? this.props.renderType : 'renderProposal'} resetFlag={this.setReset}></HtmlToPDFRenderer>
                }   
            </>
        )   
    }
}


